import React, {useState} from "react";
import axios from 'axios';

export function App() {
    const [data, setData] = useState({})
    const [location, setLocation] = useState('')

    const url = `https://api.openweathermap.org/data/2.5/weather?q=${location}&units=imperial&appid=391fdbfd3a3e77266f3d6ffc2d6c43ca`;

    const searchLocation = (event) => {
        if (event.key === 'Enter') {
            axios.get(url).then((response) => {
                setData(response.data)
            })
            setLocation('')
        }
    }

    return (
        <div className='app'>
            <div className='search'>
                <input
                    value={location}
                    onChange={event => setLocation(event.target.value)}
                    onKeyPress={searchLocation}
                    placeholder='Enter location'
                    type='text'/>
            </div>
            <div className='container'>
                <div className='top'>
                    <div className='location'>
                        <p>{data.name}</p>
                    </div>
                    <div className='temp'>
                        {data.main ? <h1>{(((data.main.temp) - 32) * 5/9).toFixed()}Â°C</h1> : null}
                    </div>
                    <div className='description'>
                        {data.weather ? <p>{data.weather[0].main}</p> : null}
                    </div>
                </div>
                {data.name !== undefined &&
                <div className='bottom'>
                    <div className='feels'>
                        {data.main ? <p className='bold'>{data.main.feels_like.toFixed()}</p> : null}
                        <p>Feels Like</p>
                    </div>
                    <div className='humidity'>
                        {data.main ? <p className='bold'>{data.main.humidity}%</p> : null}
                        <p>Humidity</p>
                    </div>
                    <div className='wind'>
                        {data.wind ? <p className='bold'>{(data.wind.speed * 1.60934).toFixed()}km/h</p> : null}
                        <p>Wind speed</p>
                    </div>
                </div>}
            </div>
        </div>
    );
}

